.ux-asides {
  display: none;
  position: relative;
  z-index: 1000;
}

.ux-asides.active {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
}

.uxAside-isOpen .ux-asides {
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.uxAside {
  position: relative;
  width: 100%;
  height: auto;
  transition: margin-top 0.3s ease, height 0.3s ease, max-width 0.3s ease;
}

.uxAside.isTall {
  height: 100% !important;
}

.uxAside.isFullscreen {
  height: 100% !important;
}

.uxAside.isAttachedTop .uxAside-button-wrapper {
  top: 100%;
  bottom: auto;
}

.uxAside .uxAside-wrap {
  width: calc(100% + 50px);
}

.uxAside .uxAside-wrap .uxAside-content {
  width: calc(100% - 50px);
}

.uxAside .uxAside-header .uxAside-header-icon {
  line-height: 40px;
}

.uxAside .uxAside-header.uxAside-noTitle {
  height: 26px;
  padding: 0;
}

.uxAside .uxAside-header.uxAside-noTitle .uxAside-header-icon {
  font-size: 16px;
  line-height: 16px;
  padding: 4px 0 0 10px;
}

.uxAside .uxAside-header.uxAside-noTitle .uxAside-header-subtitle {
  line-height: 26px;
  padding-left: 6px;
}

.uxAside .uxAside-header.uxAside-noTitle .uxAside-header-buttons {
  right: 6px;
  margin: -11px 0 0;
}

.uxAside .uxAside-header.uxAside-noTitle .uxAside-header-buttons a {
  height: 20px;
  width: 20px;
}

.uxAside.uxAside-no-transition .uxAside-header,
.uxAside.uxAside-no-transition .uxAside-header > *,
.uxAside.uxAside-no-transition .uxAside-iframe,
.uxAside.uxAside-no-transition .uxAside-wrap {
  animation: none !important;
}
